@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;600&display=swap);
.addCourseForm {
    width: 100%;
    margin: 0 auto;
    position: relative;
    display: -webkit-flex;
    display: flex;
}
.addCourseForm input[type="text"] {
    width: 80%;
    height: 60px;
    background: #f9f9f9;
    border: 1px solid #f1f1f1;
    padding: 20px;
    font-size: 1rem;
}
.addCourseForm input[type="submit"] {
    width: 20%;
    height: 60px;
    background: #00b4ea;
    border: 0;
    outline: 0;
    color: white;
    cursor: pointer;
    text-transform: uppercase;
    font-family: "Baloo Tamma 2", cursive;
    font-size: 1.15rem;
}
.ErrorMsg{
    position: absolute;
    top: 66px;
    color: tomato;
    font-size: 1.0rem;
    width: 80%;
    text-align: left;
}
.listItems {
    list-style: none;
    width: 100%;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #f1f1f1;
    padding: 20px;
}
.Actions {
    width: 19%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
#edit,
#delete {
    width: 48%;
    font-size: 1.2rem;
    color: #fff;
    height: 51px;
    border: 0;
    outline: 0;
    cursor: pointer;
}
#edit {
    background: #5f5f5f;
}
#delete {
    background: tomato;
}
.UpdateForm{
    width: 100%;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #f1f1f1;
    padding: 0 5px;
}
.updateInput{
    width: 80.35%;
    height: 51px;
    background: #fff;
    border: 1px solid #f1f1f1;
    padding: 0 15px;
    font-size: 1.1rem;
}
.updateBtn{
    width: 18.65%;
    height: 51px;
    background: #00b4ea;
    border: 0;
    outline: 0;
    color: white;
    cursor: pointer;
    text-transform: uppercase;
    font-family: "Baloo Tamma 2", cursive;
    font-size: 1.15rem;
}

.App {
    width: 80%;
    margin: 0 auto;
    background: #fff;
    padding: 60px 60px 20px;
    text-align: center;
    min-height: 537px;
    margin-top: 6%;
    box-shadow: 0 5px 4px 0px rgba(0, 0, 0, 0.02);
}
.title {
    font-size: 1.7rem;
    margin: 0px 0 40px 0;
    padding: 10px;
    font-weight: 600;
    color: tomato;
}
.teamNumber{
    font-size: 1.1rem;
    color: #b7b7b7;
}
ul {
    margin: 40px 0;
    padding: 0;
    background: #f9f9f9;
    min-height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.emptyMsg{
    color: #b7b7b7;
}
* {
  padding: 0;
    margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Baloo Tamma 2", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f3f3f3;
  font-size: 1.15rem;
}

input::-webkit-input-placeholder {
  font-family: "Baloo Tamma 2", cursive;
  font-size: 1.15rem;
}

input:-ms-input-placeholder {
  font-family: "Baloo Tamma 2", cursive;
  font-size: 1.15rem;
}

button,
input::placeholder {
  font-family: "Baloo Tamma 2", cursive;
  font-size: 1.15rem;
}

